html {
  color: #1a1a1a;
  background-color: #fdfdfd;
  font-family: Roboto;
  font-size: 20px;
  line-height: 1.5;
}

body {
  max-width: 36em;
  -webkit-hyphens: auto;
  hyphens: auto;
  overflow-wrap: break-word;
  text-rendering: optimizelegibility;
  font-kerning: normal;
  margin: 0 auto;
  padding: 50px;
}

@media (max-width: 600px) {
  body {
    padding: 1em;
    font-size: .9em;
  }
}

h1 {
  margin-top: 1.4em;
}

h2, h3 {
  margin-top: 1em;
}

code {
  color: #00f;
  font-family: Roboto Mono;
}

p {
  text-align: justify;
  margin: 1em 0;
}

a, a:visited {
  color: #1a1a1a;
}

footer {
  text-align: center;
  border-top: 1px solid #1a1a1a;
  font-size: small;
}

.wisski {
  padding-left: 5px;
}

.wisski.running {
  background-color: #9ada07;
}

.wisski.stopped {
  background-color: #ff7a7a;
}

